.hamburger {
  position: relative;
  width: 2.5em;
  height: 2.5em;
  margin-left: 0.625em;
  outline: none;
  background-color: transparent; }
  .hamburger.active .hamburger-lines {
    transform: rotate(45deg) translateY(-50%); }
    .hamburger.active .hamburger-lines:before {
      top: 0;
      transition: top 200ms ease, opacity 200ms ease;
      opacity: 0; }
    .hamburger.active .hamburger-lines:after {
      bottom: 0;
      transform: rotate(-90deg);
      transition: bottom 200ms ease, transform 200ms ease; }
  .hamburger-lines {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 0.25em;
    transform: translateY(-50%);
